import React, { Component } from 'react';
import Work from './Work';
import './Home.css';
import ProgressBar from '../../node_modules/react-bootstrap/lib/ProgressBar';


var Skills = (props) => {
	return (
		<ul className='skills'>
			{props.data.map((item) =>
				<li key={item.id}>
					<span className='skills-label'>{item.name}</span>
					<img src={item.src} width='50px'/>
					<ProgressBar bsStyle="info" now={item.width} label={`${item.width}%`} />
					<i className="fa fa-trash" aria-hidden="true" onClick={() => props.del(item.id)}></i>
				</li>
			)}
			<br/>
			<li><h3>Add new skill</h3></li>
			<li>
				<form className='addForm' id='add-skill-form'>
					<input type='text' className='form-control add-skill-input' id='label' placeholder='label'/>
					<input type='text' className='form-control add-skill-input' id='icon' placeholder='icon href'/>
					<input type='text' className='form-control add-skill-input' id='percentage' placeholder='percentage'/>
					Add item <i className="fa fa-plus-circle" aria-hidden="true" onClick={props.click}></i>
				</form>
			</li>
		</ul>
	)
};


class Home extends Component {
	constructor() {
      super();
      this.state = {
        items: [
        	{id: 0, name: 'HTML5', width: 90, src: 'https://www.w3.org/html/logo/downloads/HTML5_Logo_512.png'},
        	{id: 1, name: 'CSS3', width: 70, src: 'https://fthmb.tqn.com/WZNesKyPX1bZd2vvu1uOvb5CDPg=/768x0/filters:no_upscale()/css3-57b597e85f9b58b5c2b338de.png'},
        	{id: 2, name: 'JS', width: 60, src: 'http://qinematic.com/wp-content/uploads/2016/03/js-logo.png'},
        	{id: 3, name: 'Angular 1', width: 70, src: 'https://jaxenter.com/wp-content/uploads/2017/01/angular.png'},
        	{id: 4, name: 'React (beginner)', width: 15, src: 'https://cdn.shopify.com/s/files/1/1659/9267/products/reactjs_grande.png?v=1493774770'},
        	{id: 5, name: 'PHP (beginner)', width: 15, src: 'https://newrelic.com/assets/pages/apm/php/php-elephant-logo-bd4f9d83be8c8563248fe4793f90bae7.png'},
        	{id: 6, name: 'MySql', width: 50, src: 'http://sis-admin.su/wp-content/uploads/2015/11/ustanovka-mysql.png'},
        	{id: 7, name: 'Photoshop', width: 50, src : 'https://lh3.googleusercontent.com/3UmLl5gCUAMnzyhaJuwARDpdyn306Z1I24iGuaQsaXcgXCJgnN2toONrBJxbfpc0X9I=w300'}
        ],
		  	picture: 'src/Home/ava.jpg',
				isLike: true,
				className: 'fa fa-heart-o blue',
				addedID : 6,
   	};
   };

   toggleLike(){
		this.state.isLike ? this.setState({className : 'fa fa-heart red'}) :  this.setState({className : 'fa fa-heart-o blue'});
		this.setState({isLike : !this.state.isLike});
   };

   addNewItem(){
   	//get form values
   	let label = document.getElementById('label').value;
   	let icon = document.getElementById('icon').value;
   	let percentage = parseInt(document.getElementById('percentage').value);

   	// add new skill to the state but no render
   	this.state.items.push({id: this.state.addedID, name : label, width : percentage, src: icon});

   	//refresh the state
   	this.setState({items : this.state.items, addedID : this.state.addedID + 1});

   	document.getElementById("add-skill-form").reset();
   }

   delItem(ID){
   	//return arr of obj which id != current removing ID
   	let newArr = this.state.items.filter((item, index) => item.id != ID );
   	this.setState({items : newArr});
   }

   render(){
		return (
			<div className='main text-center'>
				<div className='container row'>
					<div className='col-md-1'></div>
					<div className='col-md-6'>
						<img src='./img/ava.jpg' alt='ava' className='ava'/>
						<i
							className={this.state.className}
							onClick={() => this.toggleLike()}>
						</i>

						<p className='bio-small' >
							I worked with different <b>libraries and frameworks</b>:
							<ul>
								<li>JQuery</li>
								<li>DragNDrop, Sliders, Swipers, Selectize, Charts ect ...</li>
								<li>Angular</li>
								<li>React</li>
							</ul>
							<b>Package managers</b> and helpers:
							<ul>
								<li>npm</li>
								<li>Bower</li>
								<li>Gulp</li>
								<li>Webpack</li>
							</ul>
							Other <b>technologies and dev tools</b> :
							<ul>
								<li>Web Socket</li>
								<li>Service Worker</li>
							</ul>
							Now, I wann'a learn Angular 2 and React. Best learning - it's practice!
							That's why I'm looking for position of React/Angular2 developer.
						</p>
					</div>
					<div className='col-md-5'>
						<h3>My Skills</h3>
						<hr/>
						<Skills data={this.state.items} click={() => this.addNewItem()} del={(ID) => this.delItem(ID)}/>
					</div>
				</div>
				<Work />
			</div>
		)
  	}
}



export default Home;


