import React, { Component } from 'react';

function WorksComp(props) {
	return (
		<div>
			{props.data.map((item) => 
				<div className='works-component' key={item.id}>
					<hr/>
					<h4> - {item.company} </h4>
					<h6> {item.position} </h6>
					<h6> {item.period} </h6>
				</div>
			)}
		</div>
   )
}

class Work extends Component {
	constructor() {
	super();
 	this.state = {
		items:[
			{
				id: 1,
				company: "BPO telecom",
				position: "Front End Developer",
				period: "2017"
			},
			{
				id: 2,
				company: "NETDM Relate Data",
				position: "Front End Developer",
				period: "Intersheep (1month)"
			},
			{
				id: 3,
				company: "Private Interpretator",
				position: "Conditioners system Installer",
				period: "2014-2016"
			}
		]
    };
  };

  render(){
		return (
			<div className='work-container'>
				<h2 className='text-center work-header'>Work Experience</h2>
				<WorksComp data={this.state.items} />
			</div>
		)
  }
}



export default Work;


