import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import './Header.css';

function HeadList(props) {
  return (
    <ul className='head-list'>
      {props.data.map((item) => (
         <li key={ item.id } onClick={ props.click.bind(this) }>
          <Link to={ item.href }>{ item.name }</Link>
         </li>
         )
      )}
    </ul>
  )
}

class Header extends Component {
  constructor() {
    super();
    this.state = {
      items:[
         {name: 'Home', id: 1, href: '/home'},
         {name:'About Me', id: 2, href: '/about'},
         {name:'Contacts', id: 3, href: '/contacts'},
         {name:'Projects', id: 4, href: '/projects'}
      ]
    };
  };

  showActive (e) {
     //remove class from all li elem's
     [...document.getElementsByTagName('li')].forEach((item) => item.className = '');
     //add class to current li
     e.target.parentElement.className += 'active';
  }
  render(){
    return (
      <header>
        <HeadList data={ this.state.items } click={ this.showActive.bind(this) }/>
      </header>
    )
  }
}


export default Header;


